.about-list {
  list-style: none;
  margin: 0;
  padding-left: 20px;
}

.about-list-item span {
  color: rgb(80, 176, 255);
}

@media (max-width: 576px) {
  .media-width {
    width: 80vw;
    margin: 0 auto;
  }
}

/* - - landscape - -  */

@media only screen and (min-device-width: 375px) and (max-device-width: 824px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  .landscape {
    padding-top: 5em;
  }
}

@media (min-width: 575px) {
  .wrapper {
    height: 100vh;
  }
}

@media (max-width: 575px) {
  .wrapper {
    margin-top: 3em;
    padding-bottom: 6em;
  }
}
